class r extends URL {
  constructor(t, e) {
    super(t, e);
  }
  endpoint(t) {
    return new r(t, this);
  }
  socket() {
    if (this.protocol === "https:")
      return this.origin.replace("https", "wss");
    if (this.protocol === "http:")
      return this.origin.replace("http", "ws");
    throw new Error("Invalid protocol");
  }
}
export {
  r as Url
};
